.Nav-darkMode {
  background-image: linear-gradient(
    to bottom,
    #0b0425,
    #130c2b,
    #1b1132,
    #241538,
    #2e183e,
    #37183e,
    #40173e,
    #49173d,
    #501435,
    #55132d,
    #581524,
    #59191b
  );
  color: rgb(113, 67, 219) !important;
}

.Nav-lightMode {
  background-image: linear-gradient(
    to bottom,
    #d16ba5,
    #c777b9,
    #ba83ca,
    #aa8fd8,
    #9a9ae1,
    #8aa7ec,
    #79b3f4,
    #69bff8,
    #52cffe,
    #41dfff,
    #46eefa,
    #5ffbf1
  );
  color: rgb(113, 67, 219);
}

.Table-lightMode {
  background-image: linear-gradient(
    to bottom,
    #5ffbf1,
    #46eefa,
    #41dfff,
    #52cffe,
    #69bff8,
    #79b3f4,
    #8aa7ec,
    #9a9ae1,
    #aa8fd8,
    #ba83ca,
    #c777b9,
    #d16ba5
  );
  color: rgb(113, 67, 219);
}

.Table-darkMode {
  background-image: linear-gradient(
    to bottom,
    #59191b,
    #581524,
    #55132d,
    #501435,
    #49173d,
    #40173e,
    #37183e,
    #2e183e,
    #241538,
    #1b1132,
    #130c2b,
    #0b0425
  );
  color: rgb(113, 67, 219);
}

.coin-search {
  margin-bottom: 54px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.coin-text {
  margin-top: 2.5em;
  margin-bottom: 32px;
  text-transform: uppercase;
  color: gold;
}

.toggle-currency {
  padding-bottom: 40px;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );
  color: aliceblue;
}

.coin-input::-webkit-input-placeholder {
  color: aliceblue;
}

.coin-input:-ms-input-placeholder {
  color: aliceblue;
}

.coin-input::placeholder {
  color: aliceblue;
}

@media (max-width: 670px) {
  .coin-input {
    width: 250px;
  }
  .coin-text {
    font-size: 1.25em;
  }
}

